import {
    Alignment,
    Classes,
    Icon,
    Navbar,
    NavbarDivider,
    NavbarGroup,
    NavbarHeading,
    ResizeSensor,
} from '@blueprintjs/core';
import React, {useCallback, useState} from 'react';
import {Link} from 'react-router-dom';

const BREAKPOINT = 650;

const Header = () => {
    const [menuCollapsed, setMenuCollapsed] = useState(false);

    const handleResize = useCallback((entries) => {
        entries.forEach((e) => {
            if (e.contentRect.width < BREAKPOINT) {
                setMenuCollapsed(true);
            } else {
                setMenuCollapsed(false);
            }
        });
    }, []);

    return (
        <ResizeSensor onResize={handleResize}>
            <Navbar>
                <NavbarGroup align={Alignment.LEFT}>
                    <NavbarHeading>
                        <Link to={'/'}>{process.env.REACT_APP_APP_NAME}</Link>
                    </NavbarHeading>
                </NavbarGroup>
                <NavbarGroup align={Alignment.RIGHT}>
                    <NavbarDivider />
                    <Link
                        to={'/'}
                        className={[Classes.BUTTON, Classes.MINIMAL].join(' ')}>
                        <Icon icon={'home'} />
                        {!menuCollapsed && (
                            <span className={Classes.BUTTON_TEXT}>Home</span>
                        )}
                    </Link>
                    <Link
                        to={'/projects'}
                        className={[Classes.BUTTON, Classes.MINIMAL].join(' ')}>
                        <Icon icon={'projects'} />
                        {!menuCollapsed && (
                            <span className={Classes.BUTTON_TEXT}>
                                Projects
                            </span>
                        )}
                    </Link>
                    <Link
                        to={'/blog'}
                        className={[Classes.BUTTON, Classes.MINIMAL].join(' ')}>
                        <Icon icon={'text-highlight'} />
                        {!menuCollapsed && (
                            <span className={Classes.BUTTON_TEXT}>Blog</span>
                        )}
                    </Link>
                </NavbarGroup>
            </Navbar>
        </ResizeSensor>
    );
};

Header.propTypes = {};

export default Header;
